.portal {
  min-height: 877px;
  padding-top: 100px;
  background: linear-gradient(rgba(0, 0, 0, 0.8)), url("../img/bg-portal.jpg"), rgba(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto 100%;
  &__wrapper {
    display: flex;
  }
  &__title {
    max-width: 330px;
    width: 70%;
    padding: 0 20px;
  }
  &__list {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
}

.mine-item {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 30px;
  padding: 40px 30px 55px 30px;
  border-radius: 15px;
  background-color: @white;
  &__content {
    display: flex;
    flex-direction: column;
    padding-right: 30px;
  }
  &__direction {
    margin-bottom: 30px;
  }
  &__title {
    margin-bottom: 10px;
  }
  &__method {
    display: flex;
    align-items: center;
    &--event {
      margin-bottom: 12px;
    }
  }
  &__icon-wrapper {
    width: 30px;
    height: 30px;
    margin-right: 15px;
    border-radius: 15px;
    background-color: @black;
    .content-center;
  }
  &__rate {
    min-width: 70px;
  }
}

@media (max-width: @tablet) {
  .portal {
    &__wrapper {
      flex-direction: column;
    }
    &__title {
      max-width: none;
      width: 100%;
      padding: 0;
    }
  }
  .mine-item {
    flex-direction: column-reverse;
    padding: 20px 15px;
    &__content {
      padding-right: 0;
    }
    &__rate {
      flex-shrink: 0;
      text-align: end;
      margin-bottom: -30px;
      font-size: 25px;
    }
    &__method {
      &--event {
        order: -1;
        margin-top: 15px;
        margin-bottom: 0;
      }
    }
  }
}

