.seminars__radio:checked,
.seminars__radio:not(:checked) {
  position: absolute;
  left: -9999px;
}
.seminars__radio:checked + label,
.seminars__radio:not(:checked) + label {
  display: inline-block;
  position: relative;
  padding-left: 38px;
  line-height: 34px;
  cursor: pointer;
}
.seminars__radio:checked + label:before,
.seminars__radio:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0px;
  top: 5px;
  width: 24px;
  height: 24px;
  border: 1px solid #dddddd;
  background-color: #ffffff;
}
.seminars__radio:checked + label:before,
.seminars__radio:not(:checked) + label:before {
  border-radius: 100%;
}
.seminars__radio:checked + label:after,
.seminars__radio:not(:checked) + label:after {
  content: "";
  position: absolute;
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.seminars__radio:checked + label:after,
.seminars__radio:not(:checked) + label:after {
  left: 6px;
  top: 11px;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  background-color: #d29500;
}
.seminars__radio:not(:checked) + label:after {
  opacity: 0;
}
.seminars__radio:checked + label:after {
  opacity: 1;
}
