.events {
  padding-bottom: 50px;
  border-bottom: 1px solid @white;
  &__arrows {
    display: none;
  }
  &__content {
    display: flex;
  }
  &__big-card {
    margin-right: 15px;
  }
  &__event-list {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: -15px;
  }
  &__small-card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    min-height: 108px;
    margin-top: 15px;
    padding: 31px;
    border-radius: 15px;
    background-color: @white;
  }
  &__date {
    padding: 3px 7px;
    border-radius: 3px;
    background-color: @orange;
  }
  &__card-text {
    margin-top: 6px;
  }
  &__dots {
    display: none;
  }
  &__button {
    margin-top: 55px;
    .center;
  }
}

.big-card {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 360px;
  &__header {
    display: flex;
    flex-direction: column;
    padding: 35px 0;
    background-color: @orange;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 8px 15px 35px 15px;
    background-color: @white;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    .text-black;
  }
  &__content-title {
    margin: 8px 0;
  }
  &__content-text {
    max-width: 225px;
    margin: 8px 0;
    .text-center;
  }
  &__text-expanded {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 10px 0;
    border-bottom: 1px solid @light-grey;
  }
  &__icon {
    width: 30px;
    height: 30px;
    margin-right: 15px;
    border-radius: 50%;
    background-color: @black;
    .content-center;
  }
  &__button {
    margin-top: 20px;
  }
}

@media (max-width: 1100px) {
  .big-card {
    max-width: 320px;
  }
}

@media (max-width: @tablet) {
  .events {
    padding-bottom: 0;
    border: none;
    &__title {
      margin-top: 10px;
      margin-bottom: 20px;
    }
    &__arrows {
      display: flex;
      justify-content: space-between;
      width: 106px;
      margin-bottom: 20px;
      .center;
    }
    &__arrow {
      width: 38px;
      height: 38px;
      background-size: 100%;
      background-repeat: no-repeat;
      cursor: pointer;
      &--left {
        background-image: url("../img/arrow-left.png");
      }
      &--right {
        background-image: url("../img/arrow-right.png");
      }
      &:hover {
        transform: scale(1.05);
      }
      &:active {
        transform: scale(0.95);
      }
      transition: transform 0.1s;
    }
    &__big-card {
      .center;
    }
    &__event-list {
      display: none;
    }
    &__dots {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: -10px;
      .center;
    }
    &__dot {
      width: 10px;
      height: 10px;
      margin-top: 20px;
      margin-left: 10px;
      border-radius: 50%;
      background-color: @white;
      cursor: pointer;
      &--active {
        background-color: @orange;
      }
    }
    &__button {
      margin-top: 20px;
    }
  }
}


