.details {
  &__req-wrapper {
    display: flex;
  }
  &__req {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    margin-top: 20px;
    padding: 75px 50px 75px 70px;
    border-radius: 15px;
    background-color: @black-transparent;
  }
  &__address {
    padding-right: 35px;
    border-right: solid 1px @white;
    & span {
      line-height: 30px;
    }
  }
  &__legal {
    display: flex;
    flex-direction: column;
    padding-bottom: 20px;
    border-bottom: solid 1px @white;
  }
  &__fact {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
  }
  &__bank {
    display: flex;
    flex-direction: column;
    padding-left: 70px;
    & span {
      line-height: 30px;
    }
  }
  &__links {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    margin-top: 20px;
    margin-left: 15px;
    padding: 90px 50px;
    border-radius: 15px;
    background-color: @black-transparent;
  }
  &__link {
    line-height: 30px;
    border-bottom: solid 1px @white;
    &:hover {
      color: @orange;
      border-bottom: solid 1px @orange;
    }
  }
}

@media (max-width: 1000px) {
  .details {
    &__req-wrapper {
      flex-wrap: wrap;
    }
    &__links {
      .center;
    }
  }
}

@media (max-width: @tablet) {
  .details {
    &__title {
      text-align: center;
    }
    &__req {
      flex-direction: column;
      padding: 20px 15px;
    }
    &__address {
      border: none;
    }
    &__legal {
      border: none;
      padding-top: 15px;
      padding-bottom: 15px;
      & span:first-child {
        margin-bottom: 20px;
        text-align: center;
      }
    }
    &__fact {
      padding-top: 15px;
      padding-bottom: 15px;
      & span:first-child {
        margin-bottom: 20px;
        text-align: center;
      }
    }
    &__links {
      display: none;
    }
    &__bank {
      padding-left: 0;
      padding-top: 15px;
      padding-bottom: 15px;
      & span:first-child {
        margin-bottom: 20px;
        text-align: center;
      }
    }
  }
}

