.button {
  min-width: 180px;
  width: fit-content;
  height: 40px;
  padding-left: 30px;
  padding-right: 30px;
  border: 0 solid @orange;
  border-radius: 20px;
  background-color: @orange;
  color: @white;
  cursor: pointer;
  &--small {
    width: 130px;
    height: 30px;
  }
  &--large {
    width: 420px;
    height: 50px;
    border-radius: 25px;
  }
  &--black {
    border-width: 2px;
    color: @white;
    background-color: @black;
  }
  .content-center;
  .text-16;

  &:hover {
    transform: scale(1.05);
  }
  &:active {
    transform: scale(0.95);
  }
  transition: transform 0.1s;
}

@media (max-width: @tablet) {
  .button {
    min-width: 290px;
    width: fit-content;
    height: 45px;
    font-size: 20px;
  }
}
