.first-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 615px;
  background: linear-gradient(rgba(0, 0, 0, 0.8)), url("../img/bg-first-screen.jpg"), rgba(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-position: center;
  &__title {
    margin-bottom: 35px;
    text-transform: uppercase;
  }
  &__img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: @orange;
    .content-center;
  }
  &__text {
    max-width: 360px;
    margin-top: 20px;
  }
}
