.courses {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 70px;
  border-bottom: 1px solid @white;
  &__catalog {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-left: -15px;
  }
  &__catalog-item {
    margin-left: 15px;
    margin-top: 15px;
    &__title {
      margin-top: 0;
    }
  }
}

.catalog-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 270px;
  padding: 40px 20px;
  border-radius: 12px;
  background-color: @dark-grey;
  &__item {
    padding: 8px 0;
  }
  &__inner {
    border-bottom: 1px solid @white;
    cursor: pointer;
    &:hover {
      color: @orange;
      border-bottom: 1px solid @orange;
    }
  }
  &__link {
    margin-top: 60px;
    .center;
  }
}

@media (max-width: @tablet) {
  .courses {
    padding-bottom: 15px;
    border: none;
    &__catalog-item:not(:first-child) {
      display: none;
    }
  }
  .catalog-item {
    width: 320px;
    padding: 40px 15px;
    &__title {
      .text-center;
    }
  }
}
