.footer {
  display: flex;
  flex-direction: column;
}

.main-footer {
  padding: 30px 0 50px 0;
  &__wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
  }
  &__about {
    max-width: 260px;
    margin-left: 15px;
    margin-right: auto;
    padding-right: 15px;
  }
  &__title {
    margin-left: -15px;
    margin-bottom: 40px;
    &--info {
      margin-left: 0;
      margin-top: 0;
    }
  }
  &__list-item {
    list-style: disc;
    line-height: 2;
  }
  &__link {
    &:hover {
      color: @orange;
    }
  }
  &__info {
    margin: 10px 0 15px 0;
    padding: 10px 30px 30px 30px;
    border: solid 1px @white;
    border-radius: 15px;
  }
  &__info-text {
    &-marg {
      margin-top: 20px;
    }
  }
  &__info-wrapper {
    display: flex;
    justify-content: space-between;
  }
  &__info-main {
    display: flex;
    flex-direction: column;
    max-width: 225px;
  }
  &__requisites {
    display: flex;
    flex-direction: column;
    margin-left: 40px;
  }
}

.law {
  padding-bottom: 15px;
  &__wrapper {
    display: flex;
    justify-content: left;
  }
  &__link {
    text-decoration: underline;
    color: #292929;
  }
  &__text {
    color: #292929;
  }
}

@media (max-width: 1000px) {
  .main-footer {
    &__about {
      flex-shrink: 0;
      margin: 0 auto;
    }
    &__wrapper {
      flex-wrap: wrap;
      justify-content: space-around;
    }
  }
}

@media(max-width: @tablet) {
  .footer .general-info__address {
      color: @orange;
    }
  .footer .general-info__phone {
    color: @orange;
  }
  .main-footer {
    &__about {
      max-width: none;
      width: 100%;
    }
    &__title {
      text-align: center;
    }
    &__list-item {
      width: fit-content;
      margin: 0 auto;
      text-align: center;
    }
    &__info-wrapper {
      flex-direction: column;
    }
    &__info-main{
      max-width: none;
      & span {
        margin: 15px auto;
        text-align: center;
      }
    }
    &__requisites {
      margin-left: 0;
      & span {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
      }
    }
  }
}
