.course {
  padding-top: 10px;
  padding-bottom: 50px;
  border-top: solid 1px @white;
  border-bottom: solid 1px @white;
  &__breadcrumbs {
    line-height: 36px;
  }
  &__text {
    line-height: 31px;
    margin-bottom: 50px;
    &--about {
      margin-bottom: 20px;
    }
  }
  &__program {
    display: flex;
    margin-bottom: 40px;
  }
  &__program-list {
    max-width: 830px;
    width: 100%;
    padding: 40px 30px;
    border-radius: 15px;
    background-color: @black-transparent;
  }
  &__program-item {
    line-height: 2;
    &::before {
      content: "- ";
    }
  }
  &__another {
    display: flex;
    flex-direction: column;
    width: 292px;
    margin-left: 15px;
    &-title {
      min-height: 65px;
      background-color: @orange;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
      .content-center;
    }
    &-content {
      display: flex;
      flex-direction: column;
      height: 100%;
      justify-content: space-between;
      padding: 60px 30px 50px 30px;
      background-color: @white;
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
    }
    &-item {
      text-decoration: underline;
      line-height: 2;
      cursor: pointer;
      &:hover {
        color: @orange;
      }
    }
  }
}

@media (max-width: @tablet) {
  .course {
    &__breadcrumbs {
      display: none;
    }
    &__text {
      text-align: center;
    }
    &__title {
      text-align: center;
      &--program {
        margin-top: 0;
        margin-bottom: 0;
        padding-top: 40px;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        background-color: rgba(255,255,255,.05);
        font-size: 25px;
      }
    }
    &__program {
      flex-direction: column;
    }
    &__program-list {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      border-bottom-right-radius: 15px;
      border-bottom-left-radius: 15px;
    }
    &__program-item {
      font-size: 16px;
      line-height: 1.8;
      text-align: center;
    }
    &__another {
      width: 100%;
      margin-left: 0;
      margin-top: 30px;
    }
    &__all-courses {
      margin-top: 130px;
    }
    &__text-bottom {
      text-align: center;
    }
    &__phone {
      display: block;
      font-size: 40px;
      text-align: center;
    }
  }
}
