.text-16 {
  font-family: "Akrobat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
}

.text-12 {
  font-family: "Akrobat", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.11;
  letter-spacing: normal;
}

.text-20 {
  font-family: "Akrobat", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
}


.text-16-bold {
  font-family: "Akrobat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
}

.text-25-bold {
  font-family: "Akrobat", "Arial", sans-serif;
  font-size: 25px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
}

.text-50-bold {
  font-family: "Akrobat", "Arial", sans-serif;
  font-size: 50px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
}

.text-40-bold {
  font-family: "Akrobat", "Arial", sans-serif;
  font-size: 40px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
}

.text-20-bold {
  font-family: "Akrobat", "Arial", sans-serif;
  font-size: 20px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-black {
  color: @black;
}

.text-white {
  color: @white;
}

.text-orange {
  color: @orange;
}

.text-grey {
  color: @grey
}

.text-dark-grey {
  color: @dark-grey;
}

.text-light-grey {
  color: @light-grey;
}

@media (max-width: @tablet) {
  .text-16 {
    font-size: 20px;
  }
  .text-25-bold {
    font-size: 35px;
  }
  .text-50-bold {
    font-size: 40px;
  }
  .text-20-bold {
    font-size: 35px;
  }
}
