.direction {
  &__prof-list {
    padding-top: 15px;
    padding-bottom: 60px;
    border-bottom: solid 1px @white;
    &--learning {
      border: none;
      padding-bottom: 30px;
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -12px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 13px;
    background-color: rgb(24, 24, 24);
    width: 275px;
    height: 249px;
    margin-left: 12px;
    margin-top: 12px;
    padding: 65px 20px;
  }
  &__img-wrapper {
    height: 75px;
    .content-center;
  }
  &__main-content {
    padding-top: 15px;
    padding-bottom: 90px;
    border-bottom: solid 1px @white;
    &--learning {
      padding-bottom: 60px;
      border: none;
    }
    &--about {
      padding-bottom: 0;
      border: none;
    }
  }
}

@media (max-width: @tablet) {
  .direction {
    &__title {
      text-align: center;
    }
    &__prof-list {
      border: none;
    }
    &__content-img {
      width: 220px;
      overflow: hidden;
      margin: 0 auto;
      &--direction {
        width: 100%;
      }
    }
    &__img {
      height: 100%;
      max-width: none;
      &--direction {
        width: auto;
        height: auto;
      }
    }
  }
}
