.testing {
  min-height: 880px;
  background: linear-gradient(rgba(0, 0, 0, 0.8)), url("../img/bg-portal.jpg"), rgba(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-position: center;
  &__content {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  &__question {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    min-height: 360px;
    padding: 45px 40px 90px 40px;
    border-radius: 15px;
    background-color: @white;
  }
  &__list {
    margin: 50px 0;
  }
  &__item {
    &::before {
      content: "- ";
    }
  }
  &__previous {
    margin-top: 50px;
  }
}

@media (max-width: @tablet) {
  .testing {
    background-size: 100% 100%;
    &__question {
      padding: 20px 15px;
    }
    &__title {
      text-align: center;
    }
    &__item {
      margin-top: 10px;
    }
    &__next {
      margin: 0 auto;
    }
    &__previous {
      margin-left: auto;
      margin-right: auto;
    }
  }
}


