.news {
  padding-bottom: 85px;
  border-bottom: 1px solid @white;
  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -10px;
  }
  &__card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 370px;
    width: 32%;
    margin-top: 10px;
    margin-left: 10px;
  }
  &__img-wrapper {
    height: 246px;
    display: flex;
    width: 100%;
  }
  &__img {
    width: 100%;
    height: auto;
    margin: auto;
  }
  &__date {
    margin-top: 10px;
    padding: 4px 8px;
    background-color: @white;
    border-radius: 5px;
  }
  &__text {
    margin-top: 10px;
    margin-bottom: auto;
  }
  &__more {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 10px;
  }
  &__more-text {
    margin-right: 10px;
    line-height: 1;
  }
  &__button {
    margin-top: 45px;
    .center;
  }
}

@media (max-width: 1000px) {
  .news {
    &__content {
      justify-content: space-around;
    }
    &__card {
      max-width: 320px;
      width: 100%;
    }
    &__card:last-child {
      display: none;
    }
    &__img-wrapper {
      height: 212px;
    }
  }
}

@media (max-width: @tablet) {
  .news {
    padding-bottom: 10px;
    border: none;
    &__title {
      margin-top: 50px;
      margin-bottom: 20px;
    }
    &__card:not(:first-child) {
      display: none;
    }
  }
}
