.header {
  display: flex;
  flex-direction: column;
}

.pre-header {
  padding: 10px 0;
  &__hamburger {
    width: 30px;
    height: 30px;
    border-radius: 5px;
    background-color: @orange;
    &-content {
      background: @black;
      box-shadow: 0 -6px @black, 0 6px @black;
      height: 2px;
      margin: -3px auto;
      width: 20px;
    }
    .content-center;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__avatar {
    margin-left: auto;
  }
  &__link {
    margin-left: 15px;
  }
}

@media (max-width: @tablet) {
  .pre-header {
    &__avatar {
      display: none;
    }
    &__button {
      min-width: 160px;
    }
  }
}

