.general-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 125px;
  background-color: @dark-grey;
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__address {
    max-width: 420px;
    flex-shrink: 2;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: 15px;
  }
  &__order {
    flex-shrink: 1;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 20px;
  }
  &__logo {
    flex-shrink: 0;
    height: 82px;
    margin-top: 10px;
    margin-bottom: 10px;
    background-image: url("../img/logo.png");
    background-repeat: no-repeat;
    background-position: center;
    .content-center;
    .center;
  }
  &__phone {
    flex-shrink: 0;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 15px;
  }
  &--bg {
    background: linear-gradient(rgba(0, 0, 0, 0.8)), url("../img/bg-header.jpg"), rgba(0, 0, 0, 0.5);
    background-repeat: no-repeat;
    background-position: center;
  }
}

@media (max-width: @tablet) {
  .general-info {
    border-top: solid 1px @white;
    border-bottom: solid 1px @white;
    &__wrapper {
      flex-direction: column;
    }
    &__address {
      max-width: 100%;
      //.text-16;
      .text-center;
      .center;
    }
    &__logo {
      order: -1;
      margin-top: 30px;
    }
    &__phone {
      .center;
    }
    &__order {
      .center;
      margin-bottom: 30px;
    }
    &--bg {
      background-size: auto 100%;
    }
  }
}

