.prog-list {
  padding-bottom: 45px;
  border-bottom: solid 1px @white;
  &__list-wrapper {
    display: flex;
    flex-wrap: wrap;
  }

  &__half-wrapper {
    display: flex;
    margin-left: -15px;
    width: 51%;
  }

  &__list {
    display: flex;
    flex-direction: column;
    margin: 15px;
  }

  &__item {
    padding: 10px 0;
  }

  &__item-text {
    border-bottom: solid 1px @white;
  }
}

.profits {
  padding-bottom: 80px;
  &__wrapper {
    display: flex;
  }
  &__example {
    margin-right: 80px;
  }
  &__img-wrapper {
    width: 375px;
  }
  &__diploma {
    width: 100%;
    height: auto;
  }
  &__list {
    margin-bottom: 40px;
  }
  &__item {
    line-height: 2;
    &::before {
      content: "- ";
    }
  }
}

@media (max-width: @tablet) {
  .prog-list {
    &__title {
      text-align: center;
    }
    &__half-wrapper {
      width: 100%;
      margin-left: 0;
    }
    &__half-wrapper:last-child {
      display: none;
    }
  }
  .profits {
    &__wrapper {
      flex-direction: column;
    }
    &__example {
      margin-right: 0;
    }
    &__title {
      text-align: center;
    }
    &__img-wrapper {
      width: 320px;
      margin-left: auto;
      margin-right: auto;
    }
    &__item {
      text-align: center;
    }
  }
}
