*,
*::after,
*::before {
  box-sizing: border-box;
  // outline: 1px solid red;
  // outline-offset: -1px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

a {
  text-decoration: none;
  color: black;
  &:hover {
    transform: scale(1.05);
  }
  &:active {
    transform: scale(0.95);
  }
  transition: transform 0.1s;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.center {
  margin-left: auto;
  margin-right: auto;
}

.content-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hidden {
  display: none;
}

@media (max-width: @tablet) {
  .wrapper {
    padding: 0 20px;
  }
}
