.seminars {
  &__content {
    display: flex;
    margin-top: 30px;
    padding-bottom: 60px;
  }
  &__list {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-right: 20px;
  }
  &__event {
    margin-bottom: 40px;
  }
  &__filter-wrapper {
    display: flex;
    flex-direction: column;
    max-width: 277px;
    width: 50%;
  }
  &__filter {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 20px;
    padding: 35px 15px 100px 15px;
    border-radius: 15px;
    background-color: @black-transparent;
  }
  &__filter-title {
    margin-bottom: 30px;
    width: 100%;
  }
}

.event {
  display: flex;
  flex-direction: column;
  padding: 35px;
  border-radius: 15px;
  background-color: @white;
  .text-black;
  &__header {
    display: flex;
    align-items: center;
  }
  &__date {
    padding: 5px 35px;
    border-radius: 5px;
    background-color: @orange;
    .text-white;
  }
  &__direction {
    margin-left: 15px;
    margin-right: auto;
  }
  &__content {
    display: flex;
    margin-top: 24px;
  }
  &__img-wrapper {
      width: 206px;
      height: 133px;
    background-color: @light-grey;
      .content-center;
  }
  &__img {
    width: auto;
    height: auto;
  }
  &__content-wrapper {
    display: flex;
    flex-direction: column;
    margin-left: 40px;
  }
  &__actions {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  &__link {
    margin-left: 20px;
    border-bottom: solid 1px @black;
  }
  &__title {
    margin-bottom: 10px;
  }
  &__speakers {
    line-height: 22px;
  }
}


.seminars__radio:checked,
.seminars__radio:not(:checked) {
  position: absolute;
  left: -9999px;
}

.seminars__radio:checked + label,
.seminars__radio:not(:checked) + label {
  display: inline-block;
  position: relative;
  padding-left: 38px;
  line-height: 34px;
  cursor: pointer;
}

.seminars__radio:checked + label:before,
.seminars__radio:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0px;
  top: 5px;
  width: 24px;
  height: 24px;
  border: 1px solid #dddddd;
  background-color: @white;
}

.seminars__radio:checked + label:before,
.seminars__radio:not(:checked) + label:before {
  border-radius: 100%;
}

.seminars__radio:checked + label:after,
.seminars__radio:not(:checked) + label:after {
  content: "";
  position: absolute;
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.seminars__radio:checked + label:after,
.seminars__radio:not(:checked) + label:after {
  left: 6px;
  top: 11px;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  background-color: @orange;
}

.seminars__radio:not(:checked) + label:after {
  opacity: 0;
}

.seminars__radio:checked + label:after {
  opacity: 1;
}

@media (max-width: @tablet) {
  .seminars {
    &__title {
      margin: 15px 0;
    }
    &__month {
      display: block;
      text-align: center;
      color: @orange;
    }
    &__filter-wrapper {
      max-width: none;
      width: 100%;
    }
    &__content {
      flex-direction: column-reverse;
    }
    &__filter {
      &--calendar {
        display: none;
      }
    }
  }

  .event {
    padding: 0 0 35px 0;
    &__header {
      flex-wrap: wrap;
    }
    &__date {
      width: 100%;
      border-top-right-radius: 15px;
      border-top-left-radius: 15px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      text-align: center;
      padding: 20px 0;
      font-size: 40px;
    }
    &__direction {
      padding: 20px 15px;
      font-size: 20px;
    }
    &__rate {
      padding: 20px 15px;
      font-size: 20px;
    }
    &__content {
      flex-direction: column;
      align-items: center;
      margin-top: 0;
      padding: 0 15px;
    }
    &__img-wrapper {
      width: 100%;
      height: auto;
      min-height: 155px;
    }
    &__content-wrapper {
      align-items: center;
      margin-left: 0;
    }
    &__title {
      margin-top: 20px;
      margin-bottom: 30px;
      text-align: center;
    }
    &__speakers {
      text-align: center;
    }
    &__actions {
      flex-direction: column;
    }
    &__link {
      margin-top: 10px;
      margin-left: 0;
    }
  }
}
