.main-menu {
  &__list {
    display: flex;
    justify-content: center;
    padding: 20px 0;
  }
  &__item {
    display: inline-block;
    padding: 0 3.5%;
    border-right: 1px solid @white;
    &:last-child {
      border-right: none;
    }
  }
}

.item-active {
  color: @orange;
  border-bottom: 1px solid @orange;
}

@media (max-width: 1100px) {
  .main-menu {
    &__item {
      padding: 0 2%;
    }
  }
}

@media (max-width: @tablet) {
  .main-menu {
    display: none;
  }
}
