.login {
  min-height: 636px;
  background: linear-gradient(rgba(0, 0, 0, 0.8)), url("../img/bg-login.jpg"), rgba(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-position: center;
  &__base {
    max-width: 835px;
    min-height: 445px;
    border-radius: 15px;
    background-color: rgba(0,0,0,0.5);
    padding: 30px 0 90px 0;
    margin: 90px auto 0 auto;
  }
  &__title {
    margin-bottom: 55px;
  }
  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__input {
    width: 420px;
    height: 50px;
    margin-bottom: 30px;
    padding: 0 30px;
    border-radius: 25px;
    &:focus-visible {
      outline: none;
      border: solid 1px @orange;
    }
  }
}

@media (max-width: @tablet) {
  .login {
    min-height: 470px;
    &__base {
      padding: 30px 20px;
      margin: 40px auto 0 auto;
    }
    &__input {
      max-width: 500px;
      width: 100%;
      height: 45px;
      border-radius: 23px;
    }
    &__button {
      max-width: 500px;
      width: 100%;
      min-width: 100px;
    }
  }
}

