.reviews {
  padding-bottom: 70px;
  border-bottom: 1px solid @white;
  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 70px 7%;
    border-radius: 30px;
    background-color: @white;
  }
  &__list {
    display: flex;
    justify-content: center;
  }
  &__item {
    max-width: 270px;
    padding: 20px;
    position: relative;
    .content-center;
  }
  &__img-wrapper {
    position: relative;
    box-shadow: 0 0 30px 5px rgba(0,0,0,0.3);
    cursor: pointer;
    &:hover {
      box-shadow: none;
    }
    &:hover > .reviews__item-cover {
      display: flex;
      opacity: 1;
      transition: opacity 0.3s;
    }
  }
  &__item-img {
    width: 100%;
    height: auto;
  }
  &__item-cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    .content-center;
    opacity: 0;
    transition: opacity 0.3s;
  }
  &__arrows {
    position: absolute;
    top: calc(50% - 20px);
    left: 0;
    width: 100%;
  }
  &__arrow {
    position: absolute;
    width: 40px;
    height: 40px;
    cursor: pointer;
    &--left {
      left: 2.5%;
      background-image: url("../img/arrow-left.png");
    }
    &--right {
      right: 2.5%;
      background-image: url("../img/arrow-right.png");
    }
  }
}

@media (max-width: @tablet) {
  .reviews {
    padding-bottom: 10px;
    border: none;
    &__title {
      margin-bottom: 80px;
    }
    &__content {
      padding: 10px 0;
    }
    &__item:not(:first-child) {
      display: none;
    }
    &__arrows {
      order: -1;
      position: relative;
      top: -75px;
      left: auto;
      width: 106px;
      margin: 0 auto;
    }
  }
}
